import React from 'react'
import './Mymain.css'
import { useRef } from 'react';

import { Canvas, useFrame } from 'react-three-fiber';
import { Circle, softShadows, Sphere } from "@react-three/drei"
import { MeshWobbleMaterial } from '@react-three/drei';
import { OrbitControls } from '@react-three/drei';
import { useLoader } from 'react-three-fiber';
import * as THREE from 'three';

export default function Mymain() {
    function Sphere({position , args ,color ,speed ,mapName}){
        const map = new THREE.TextureLoader().load(mapName);


        const mesh =useRef(null);
        useFrame(()=>{mesh.current.rotation.x =mesh.current.rotation.y += 0.01});
    
        
        return(
        <mesh  castShadow position={position} ref={mesh}>
        <sphereGeometry attach="geometry" args={args}/>
        <meshStandardMaterial attach="material" color={color} map={map} speed={speed} factor={0.6}/> 
        </mesh>
        );
        

    }
  return (
    <div className='main '>
      <div className='row'>
        <div className='col-lg-6 col-md-6 col-sm-12 col-xs-12 ' data-aos="fade-right" data-aos-delay="300">
        <h1 className='heading text-white ms-5' data-aos="flip-up" data-aos-delay="700"><span className="span">A</span>B<span className='span'>O</span>U<span className='span'>T</span>  <span className='span'>M</span>E</h1>
        <p className='skillw w-75 ms-5 mt-4'>Hello,<br/>
        I'am Aditya Arun Sonawane 
        I have completed Electronics and Telecommunication Engineering from Mumbai University in 2022 .
          I have gained my frontend development , web developer and designing  knowledge  from various platforms. I have done several projects and some 
          of them are designed and developed on react.js Framework with the help of bootstrap and various Tools of animations.
          Currently i'am learning various skills related to Web Development and also works as Freelancer Web developer. 
          </p>
          <a href="https://github.com/sonawane4868" target="_blank" rel="noreferrer"><img src='./github.png' className='mainimg mt-4 ms-5'  /></a>
          <a href='https://in.linkedin.com/in/adityasonawaneee?trk=people-guest_people_search-card' target="_blank" rel="noreferrer"><img src='./linkedin.png' className='mainimg mt-4 ms-3' /></a>
          <a href="https://www.instagram.com/sonawane4868" target="_blank" rel="noreferrer"><img src='./insta.png' className='mainimg mt-4 ms-3' /></a>
          <a href="https://www.facebook.com/addi.sonawane.7" target="_blank" rel="noreferrer"><img src='./face.png' className='mainimg mt-4 ms-3' /></a>
        </div>
        <div className='col-lg-6 col-md-6 col-sm-12 col-xs-12 div2  text-center sm-d-block' data-aos="fade-left">
           
        <Canvas shadows camera={{position:[-5,2,10] ,fav:[60] }}  data-aos="fade-up" data-aos-delay="1000" data-aos-duration="500" >
    
    <ambientLight intensity={0.3}/>
    
   
   <group>
    
    <Sphere position={[0,1,0]} args={[5]} mapName="./gol.jpg" speed={2}/>
    
   </group>
   
    
    <OrbitControls/>
   </Canvas>
   </div>
   </div>
        
      
    </div>
  )
}
